.skills-container{
    margin: 4rem 0;
    position: relative;
}

.skills-container::after,
.skills-container::before{
    content: " ";
    width: 28rem;
    height: 28rem;
    margin-left: 77px;
    border-radius: 28rem;
    background: #7c66e3;
    position: absolute;
    z-index: -1;    
    filter: blur(200px);
}

.skills-container::after{
    top: -3rem;
    left: -5rem;
}

.skills-container::before{
    background: #9e51b9;
    margin-right: 146px;
    bottom: 4rem;
    right: 0rem;
}

.skills-container h5{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
    margin-left: 70px;
}

.skills-content{
    display: flex;
    align-items: flex-start;
    gap: 3rem;
}

.skills{
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
}

.skills-info{
    flex: 1;
    
}

@media (max-width : 1025px){
    .skills{
        padding-left: 1rem;
    }

    .skills,
    .skills-content{
        grid-gap: 2rem;
    }
}

@media (max-width : 768px){
    .skills-content{
       flex-direction: column-reverse;
       gap: 3rem;
    }

    /* .skills,
    .skills-info{
        width: 100%;
    } */

    /* .skills-info{
        justify-content: space-around;
    } */
    .skills-container h5{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }

    .skills-container::after,
    .skills-container::before{
        width: 18rem;
        height: 18rem;
    }
}

